@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Lobster+Two:ital,wght@0,400;0,700;1,400;1,700&display=swap);
.form__button{font:inherit;color:inherit;cursor:pointer;border-radius:4px;border:1px solid #ccc;background-color:transparent;font-size:14px;padding:5px;transition:background-color 250ms cubic-bezier(0.4, 0, 0.2, 1)}.form__button:hover,.form__button:focus{background-color:#03a9f4}.form{display:flex;flex-direction:column;justify-content:space-between;padding:20px}.form__label{display:block;margin-bottom:30px}.form__input{width:100%;padding:10px;display:block;border-radius:4px;border:1px solid #ccc}.form__name{display:block;margin-bottom:10px;font-weight:500}.form__button{max-width:100px}

.title{text-align:center;margin-bottom:0}.title.h1{font-size:3rem}.title.h2{font-size:1.5rem}.title.h3{font-size:1.17rem}.title.h4{font-size:0.93rem}.title.h5{font-size:0.83rem}.title.h6{font-size:0.67rem}

.filter__wrapper{width:100%;display:flex;flex-direction:column;align-items:flex-end;margin-bottom:30px}.filter__input{display:block;padding:10px;border-radius:4px;border:1px solid #ccc;min-width:390px;font-family:"Helvetica Neue",-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans",sans-serif}.filter__input::-webkit-input-placeholder{opacity:0.2;font-style:italic}.filter__input:-ms-input-placeholder{opacity:0.2;font-style:italic}.filter__input::placeholder{opacity:0.2;font-style:italic}.filter__name{display:block;font-size:1.2rem;margin-bottom:10px}

.message{font-size:1.5rem;margin-top:50px;text-align:center;color:#303f9f;text-shadow:0 0 100px #03a9f4,0 0 20px #03a9f4,0 0 40px #03a9f4,0 0 80px #03a9f4,0 0 120px #03a9f4,0 0 160px #03a9f4}

.icon__button{margin:0;padding:15px 20px;border:none;border-radius:50%;color:#fff;font:inherit;background-color:inherit;cursor:pointer;display:inline-flex;align-items:center;justify-content:center}@media screen and (min-width: 768px){.icon__button:not(:last-child){padding:15px 40px}}svg{width:30px;height:30px}

.contacts__list{padding:0;margin:0;list-style:none}.delete__btn{font:inherit;color:inherit;cursor:pointer;border-radius:4px;border:1px solid #ccc;background-color:transparent;font-size:14px;padding:5px;transition:background-color 250ms cubic-bezier(0.4, 0, 0.2, 1)}.delete__btn:hover,.delete__btn:focus{background-color:#03a9f4}.contacts__item{display:flex;align-items:center;border-radius:6px;padding:5px 10px;margin-bottom:10px;box-shadow:rgba(3,169,244,0.17) 0px -23px 25px 0px inset,rgba(3,169,244,0.15) 0px -36px 30px 0px inset,rgba(3,169,244,0.1) 0px -79px 40px 0px inset,rgba(0,0,0,0.06) 0px 2px 1px,rgba(3,169,244,0.09) 0px 4px 2px,rgba(0,0,0,0.09) 0px 8px 4px,rgba(3,169,244,0.09) 0px 16px 8px,rgba(0,0,0,0.09) 0px 32px 16px}@media screen and (min-width: 768px){.contacts__item{padding:10px}}@media screen and (min-width: 1140px){.contacts__item{margin-bottom:20px;padding:20px;width:80%;margin-left:auto;margin-right:auto}}.contacts__name,.contacts__number{color:#303f9f;font-size:15px;margin-bottom:0}@media screen and (min-width: 768px){.contacts__name,.contacts__number{font-size:25px}}.contacts__name{margin-right:20px}@media screen and (min-width: 768px){.contacts__name{margin-right:150px}}.delete__btn{margin-left:auto}.react__icons--user,.react__icons--phone{color:#303f9f;display:inline-block;width:10px;height:10px;margin-right:5px}@media screen and (min-width: 768px){.react__icons--user,.react__icons--phone{width:20px;height:20px;margin-right:15px}}.contacts__link{text-decoration:none;display:flex;align-items:center;width:90%}

.clock__face{font-weight:500;margin-bottom:0;color:#303f9f;text-shadow:0 0 100px #03a9f4,0 0 20px #03a9f4,0 0 40px #03a9f4,0 0 80px #03a9f4,0 0 120px #03a9f4,0 0 160px #03a9f4}

.modal__backdrop{position:fixed;top:0;left:0;width:100vw;height:100vh;background-color:rgba(0,0,0,0.5)}.modal__content{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);max-width:400px;width:100%;padding:12px;background-color:#fff;border-radius:3px;box-shadow:0px 2px 1px -1px rgba(0,0,0,0.2),0px 1px 1px 0px rgba(0,0,0,0.14),0px 1px 3px 0px rgba(0,0,0,0.12)}

.flex__wrapper{display:flex;align-items:center;justify-content:space-between;max-width:400px}@media screen and (min-width: 768px){.flex__wrapper{margin-bottom:11px}}@media screen and (min-width: 1140px){.flex__wrapper{margin-bottom:0}}

.container{width:100%;margin-left:auto;margin-right:auto;padding-left:10px;padding-right:10px}@media screen and (min-width: 420px){.container{width:420px}}@media screen and (min-width: 768px){.container{width:768px;padding-left:75px;padding-right:75px}}@media screen and (min-width: 1140px){.container{width:1140px;padding-left:71px;padding-right:71px}}

body{margin:0;background-color:#fff;color:#303f9f;line-height:1.5;font-family:"Lobster",-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans",sans-serif;background-image:url("http://bgfons.com/upload/paper_grid_PNG5421.png")}@media screen and (min-width: 768px){body{background:url(/goit-react-hw-07-phonebook/static/media/a-book-gd4a363064_1280.a107c640.png)}}@media screen and (min-width: 1140px){body{background-repeat:no-repeat;background-size:cover}}img{display:block;max-width:100%;height:auto}h1,h2,h3,h4,h5,h6{margin-top:0;margin-bottom:10px}p{margin-top:0}

